.char{
    &__info{
        padding: 25px;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
        background-color: #FFF;
        &_name{
            text-transform: uppercase;
            font-weight: bold;
            font-size: 22px;
            line-height: 29px;
        }
    }
    &__basics{
        display: grid;
        grid-template-columns: 150px auto;
        column-gap: 25px;
        img{
            width: 150px;
            height: 150px;
            object-fit: cover;
        }
    }
    &__btns{
        margin-top: 35px;
        a:nth-child(2){
            margin-top: 10px;
        }
    }
    &__descr{
       margin-top: 15px;
       font-weight: bold;
       font-size: 14px;
       line-height: 16px; 
    }
    &__comics{
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        margin: 10px 0px;
        &-list{
            position: relative;
        }
        &-item{
            width: 100%;
            padding: 4px 10px;
            margin-top: 10px;
            line-height: 25px;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        }
    }
    &__select{
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        text-align: center;
    }
}