@import '../../style/variables.scss';
.char{
    &__content{
        margin-top: 50px;
        display: grid;
        grid-template-columns: 650px 425px;
        column-gap: 25px;
        align-items: start;
    }
    &__grid{
        display: grid;
        grid-template-columns: repeat(3, 200px);
        row-gap: 25px;
        column-gap: 30px;
    }
    &__item{
        height: 318px;
        width: 200px;
        background-color: $dark;
        padding: 15px;
        cursor: pointer;
        transition: 0.3s transform;
        img{
            width: 200px;
            height: 200px;
            object-fit: cover;
            transform: translate(-15px, -15px);
        }
        &_selected{
            transform: translateY(-15px);
            box-shadow: 0 5px 20px $main-color;
        }
        span{
            color: #FFF;
            text-transform: uppercase;
            font-size: 22px;
            font-weight: bold;
            line-height: 26px;
        }
    }
}